.container {
	background-color: red;
	position: fixed;
	max-width: 222px;
	min-width: 222px;
	padding-inline: 24px;
	height: 100vh;
	float: left;
}

.logo {
	padding-block: 20px;
	height: 30px;
	width: 222px;
	display: flex;
	flex-direction: row;
	
	p {
		font-size: 1.4em;
    margin: 0;
		margin-left: 12px;
		font-weight: bold;
	}
}

.sidebar_contents{
  padding-block: 16px;
	height: auto;
	width: 100%;
}
.sidebar_menu{
	margin-block: 8px;
	padding-block: 8px;

	p {
		font-size: 17px;
		font-weight: 600;
		margin: 0;
	}
}